import React from "react";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";
import senate from "../images/senate_picture.jpg"
import design1 from "../images/designplan1.jpg"
import "./buildingPlans.css";
class buildingPlans extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
        <div className="parentContainerBuilding">
                 <div className ="carouselParent">
        <Carousel className="carousel"
        showThumbs= {false}
        dynamicHeight={true}
        autoPlay={true}
        emulateTouch={true}
        swipeable={true}
        stopOnHover={true}
        interval= {3000}
        infiniteLoop={true}
        showStatus={true}
        showIndicators={true}
        useKeyboardArrows={true}
        transitionTime={200}
        swipeScrollTolerance={5}
        >
            
                <img className="image" src={design1} alt=""/>
          
         
           
        </Carousel>
      </div>
        </div>
 
    );
  }
}
export default buildingPlans;
